



















































































































































































	export default {
		data() {
			return {
				tabLists: [
          {
            name: '新手任务',
            type: 1
          },
          {
            name: '日常任务',
            type: 2
          }
        ],
        tabIndex: 0,
        dreamShow: false,
        dreamArr: [
          {
            amount: '0.00',
            id: 0,
            over: 0
          }
        ],
        getDreamArr: false,
        dreamIndex: 0,
        dreamPercent: 0,
        confirmIndex: 0,
        weekTask: {
          banker_init: {
            finish_num: 0,
            is_finish: false,
            toward: 0
          },
          invite_newer: {
            finish_num: 0,
            is_finish: false,
            toward: 0
          },
          team_apply: {
            finish_num: 0,
            is_finish: false,
            toward: 0
          },
          is_finish: false
        },
        teamTask: {
          0: {
            finish_num: 0,
            is_finish: false,
            toward: 0
          }
        },
        taskLists: [],
        taskIsFinish: false,
        taskLoading: false,
        situationShow: false,
        redpacketShow: false,
        taskRedId: 0,
        opening: false,
        opened: false,
        isClickRed: false,
        scrrollTop: 0,
        nowScrollTop: 0,
        money: 0.00,
        weekTaskArr: [],
        weekTaskState: false
			};
		},
    computed: {
      config() {
        return this.$store.state.config
      },
      userInfo() {
        return this.$store.state.userInfo
      },
      constant() {
        return this.$constant
      },
      role() {
        return this.userInfo.role
      },
      bonus() {
        return this.userInfo.bonus
      },
      progressPercent() {
        return parseFloat(this.bonus.replace(',', '')) / parseFloat(this.dreamArr[this.confirmIndex].amount) * 100
      },
      progressPercentTofixed() {
        return this.progressPercent.toFixed(2)
      },
      blockSize() {
        let size = uni.upx2px(64) / 3 * 2
        return size >= 28 ? 28 : size
      }
    },
		onLoad() {
			if (this.role > 0) {
        this.$http({
          url: this.$apis.dreamLevel,
          method: 'GET',
          data: null,
          cb: (err, data) => {
            if (!err && data.success) {
              this.dreamArr = data.data
              this.getDreamArr = true
              this.confirmIndex = this.userInfo.task_level
              this.updateWeekTask()
            } else {
              uni.showToast({
              	title: data ? data.message : '获取梦想收入等级失败',
                icon: 'none'
              })
            }
          }
        })
        this.updateTask(() => {
          if (this.taskIsFinish && this.tabLists[0].type === 1) {
            let s = this.tabLists.pop()
            this.tabLists.unshift(s)
            setTimeout(() => {
              this.updateTask()
            }, 50)
          }
        })
      }
		},
    onShow() {
    	if (this.role === 0) {
        this.showTips()
      }
    },
    onHide() {
    	this.nowScrollTop = this.scrrollTop
    },
    methods: {
      showTips() {
        uni.showModal({
        	title: '提示',
          content: '您是临时用户，无权使用此功能，请先免费升级为普通会员继续使用。',
          cancelText: '知道了',
          confirmText: '去升级',
          success: res => {
          	if (res.confirm) {
              this.$navigateTo({
              	url: '/pages/mine/agent'
              })
            }
          }
        })
      },
      gotoAgent() {
        this.$navigateTo({
        	url: '/pages/mine/agent'
        })
      },
      gotoBanker() {
        this.$navigateTo({
        	url: '/pages/index/member'
        })
      },
      gotoCertificate() {
        this.$navigateTo({
        	url: '/pages/mine/certificate'
        })
      },
      gotoEarning() {
        this.$navigateTo({
        	url: '/pages/mine/earning?tab=1'
        })
      },
      tabClick(n) {
        if (this.tabIndex === n) return
        if (this.role === 0) {
          this.showTips()
        } else {
          this.tabIndex = n
          this.updateTask()
        }
      },
      updateTask(cb) {
        if (this.taskLoading) return
        this.taskLoading = true
        this.taskLists = []
        this.$http({
          url: this.$apis.task,
          method: 'GET',
          data: {
            type: this.tabLists[this.tabIndex].type
          },
          cb: (err, data) => {
            if (!err && data.success) {
              this.taskLists = data.data.data
              this.taskIsFinish = data.data.is_finish
              this.isClickRed = false
              if (typeof cb === 'function') {
                cb()
              }
            } else {
              uni.showToast({
              	title: data ? data.message : '任务列表获取失败',
                icon: 'none'
              })
            }
            this.taskLoading = false
          }
        })
      },
      updateWeekTask() {
        if (this.confirmIndex !== 0) {
          this.$http({
            url: this.$apis.task,
            method: 'GET',
            data: {
              type: 3
            },
            cb: (err, data) => {
              if (!err && data.success) {
                this.weekTask = data.data.data
                this.weekTask.is_finish = data.data.is_finish
              } else {
                uni.showToast({
                	title: data ? data.message : '周任务获取失败',
                  icon: 'none'
                })
              }
              this.taskLoading = false
            }
          })
          this.$http({
            url: this.$apis.task,
            method: 'GET',
            data: {
              type: 5
            },
            cb: (err, data) => {
              if (!err && data.success === true) {
                this.teamTask = data.data.data
              } else {
                uni.showToast({
                	title: data ? data.message : '团队任务获取失败',
                  icon: 'none'
                })
              }
              this.taskLoading = false
            }
          })
        }
      },
      gotoFinash(url) {
        let str = url.split('#/').pop()
        switch (str) {
          case 'invite':
            this.$navigateTo({
            	url: '/pages/invite/invite'
            })
            break
          case 'material':
            this.$navigateTo({
            	url: '/pages/spread/material'
            })
            break
          case 'apply':
            this.$navigateTo({
            	url: '/pages/credit/index'
            })
            break
          case 'banker':
            this.$navigateTo({
            	url: '/pages/index/member'
            })
            break
          case 'cash':
            this.$navigateTo({
            	url: '/pages/mine/cash'
            })
            break
          case 'account':
            this.$navigateTo({
            	url: '/pages/mine/account'
            })
            break
          case 'task':
            this.showDreamPop()
            break
          case 'query':
            this.$navigateTo({
            	url: '/pages/credit/query'
            })
            break
        }
      },
      scroll(e) {
        this.scrrollTop = e.detail.scrollTop
      },
      showPacket(id) {
        if (!id) {
          uni.showToast({
          	title: '完成任务才能抽红包哟',
            icon: 'none'
          })
        } else {
          this.redpacketShow = true
          this.opening = false
          this.opened = false
          this.taskRedId = id
        }
      },
      openRedPacket() {
        if (this.opening || this.isClickRed) return
        this.opening = true
        this.$http({
          url: this.$apis.taskRedpacket,
          method: 'POST',
          data: {
            id: this.taskRedId
          },
          cb: (err, data) => {
            if (!err && data.success) {
              this.money = data.data
              this.isClickRed = true
              setTimeout(() => {
                this.opening = false
                this.opened = true
                this.$getInfo()
              }, 1000)
            } else {
              setTimeout(() => {
                uni.showToast({
                	title: '抽红包失败，请稍后再试',
                  icon: 'none'
                })
                this.opening = false
                this.opened = false
              }, 1000)
            }
          }
        })
      },
      closeRedPop() {
        if (this.isClickRed) {
          this.updateTask(() => {
            setTimeout(() => {
              this.nowScrollTop = this.scrrollTop
            }, 16.67)
          })
        }
        this.redpacketShow = false
      },
      showDreamPop() {
        if (this.role === 0) {
          this.showTips()
        } else {
          if (!this.getDreamArr) return
          this.dreamShow = true
          this.dreamIndex = this.confirmIndex
          this.dreamPercent = this.dreamIndex * 10
        }
      },
      dragDreamBar(e) {
        this.dreamPercent = e.detail.value
        this.percentHandle(this.dreamPercent, false)
      },
      stopDragDreamBar(e) {
        this.dreamPercent = e.detail.value
        this.percentHandle(this.dreamPercent, true)
      },
      percentHandle(n, b) {
        this.dreamIndex = Math.round(this.dreamPercent / 10)
        setTimeout(() => {
          if (b) this.dreamPercent = this.dreamIndex * 10
        }, 16.67)
      },
      confirmDream() {
        let _this = this
        if (_this.dreamIndex === 0) {
          uni.showToast({
          	title: '请设置具体数值',
            icon: 'none'
          })
        } else {
          _this.confirmIndex = _this.dreamIndex
          _this.hideDreamPop()
          _this.$http({
            url: _this.$apis.setDream,
            method: 'PUT',
            data: {
              level: _this.dreamArr[_this.confirmIndex].id
            },
            cb: (err, data) => {
              if (!err && data.success) {
                _this.$getInfo(() => {
                  _this.confirmIndex = _this.userInfo.task_level
                  _this.updateTask()
                  _this.updateWeekTask()
                })
              } else {
                uni.showToast({
                	title: data ? data.message : '设置梦想收入失败，请稍候再试',
                  icon: 'none'
                })
              }
            }
          })
        }
      },
      hideDreamPop() {
        this.dreamShow = false
      },
      showSituation() {
        if (this.weekTaskArr.length <= 0) {
          for (let index = 0; index < 52; index ++) {
            this.$set(this.weekTaskArr, index, -1)
          }
        }
        if (!this.weekTaskState) {
          this.$http({
            url: this.$apis.weekTask,
            method: 'GET',
            data: null,
            cb: (err, data) => {
              if (!err && data.success) {
                this.weekTaskState = true
                for (let index = 0; index < data.data.length; index ++) {
                  this.$set(this.weekTaskArr, index, data.data[index])
                }
                this.$set(this.weekTaskArr, data.data.length, 2)
              } else {
                uni.showToast({
                	title: data ? data.message : '获取数据失败，请稍后再试',
                  icon: 'none'
                })
                return
              }
            }
          })
        }
        this.situationShow = true
      },
      hideSituation() {
        this.situationShow = false
      },
      gotoRecruit() {
        this.$navigateTo({
        	url: '/pages/partner/recruit'
        })
      },
      gotoInvite() {
        if (this.config.module_control_invite === this.constant.MODULE_STATE_OPEN) {
          this.$navigateTo({
          	url: '/pages/invite/index'
          })
        } else {
          uni.showToast({
          	title: '邀请活动暂未开放',
            icon: 'none'
          })
        }
      }
    }
	}
